import "./MapComponent.css";
import { useEffect, useRef, useState } from "react";
import {
  MapContainer,
  TileLayer,
  GeoJSON,
  useMap,
  Marker,
  useMapEvents,
} from "react-leaflet";
import { GeoJsonObject } from "geojson";
import {
  GeoJSON as LeafletGeoJSON,
  LatLngTuple,
  Layer,
  PathOptions,
} from "leaflet";
import chroma from "chroma-js";

import {
  SAMSPA_API_ROOT,
  OPEN_ROUTE_API_KEY,
  OPEN_ROUTE_API_ROOT,
} from "./constants";
import SearchComponent from "./SearchComponent";

interface BlockfaceSummary {
  blockface_id: string;
  unitdesc: string;
  side: string;
  parking_category: string;
  parking_spaces: number;
  parking_time_limit: number;
  start_time_wkd: string;
  end_time_wkd: string;
  start_time_sat: string;
  end_time_sat: string;
  start_time_sun: string;
  end_time_sun: string;
  longitude: number;
  latitude: number;
  geometry: string;
}

interface BlockfaceOccupancyDataResult {
  blockface_id: string;
  weekday: string;
  times: string[];
  historical_averages: number[];
}

interface BlockfaceGeojson {
  type: string;
  name: string;
  features: GeoJSON.Feature<GeoJSON.Geometry, any>[];
}

type SetViewOnClickProps = {
  coords: LatLngTuple;
};

enum ParkingCategory {
  UnrestrictedParking = "Unrestricted Parking",
  NoParkingAllowed = "No Parking Allowed",
  RestrictedParkingZone = "Restricted Parking Zone",
  None = "None",
  TimeLimitedParking = "Time Limited Parking",
  PaidParking = "Paid Parking",
  CarpoolParking = "Carpool Parking",
}

function MapComponent() {
  const geoJsonRef = useRef<LeafletGeoJSON | null>(null);
  const [blockfaceGeojson, setBlockfaceGeojson] = useState({
    type: "",
    name: "",
    features: [] as GeoJSON.Feature<GeoJSON.Geometry, any>[],
  });
  const [latitude, setLatitude] = useState(47.615756);
  const [longitude, setLongitude] = useState(-122.339591);
  const [noResult, setNoResult] = useState(false);
  const [showSearchResult, setShowSearchResult] = useState(false);
  const [blockfaceWeight, setBlockfaceWeight] = useState(7);

  function SetViewOnClick({ coords }: SetViewOnClickProps) {
    const map = useMap();
    map.setView(coords, map.getZoom());

    return null;
  }

  const getCurrentIsoWeekday = () => {
    const date = new Date();
    // Date weekday (0 = Sunday, 6 = Saturday) -> ISO weekday (1 = Monday, 7 = Sunday)
    return date.getDay() > 0 ? date.getDay() : 7;
  };

  const getCurrentIsoTime = () => {
    const date = new Date();
    // ISO time
    return date
      .toLocaleTimeString("en-US", { hour: "2-digit", minute: "2-digit" })
      .split(" ")[0];
  };

  const getExpectedNumSpaces = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any>
  ): number => {
    const times = feature.properties.times;
    const historicalAverages = feature.properties.historical_averages;
    const parkingSpaces = feature.properties.parking_spaces;

    const time = getCurrentIsoTime();
    const idx = times.indexOf(time);
    if (idx > -1) {
      const historicalAverage = historicalAverages[idx] || 0;
      return parkingSpaces * (1 - historicalAverage);
    }
    return 0;
  };

  function DragOrZoomEvent() {
    useMapEvents({
      drag: () => {
        setShowSearchResult(false);
        setNoResult(false);
      },
      zoom: (e) => {
        setBlockfaceWeight(e.target._zoom - 10);
      }
    });
    return null;
  }

  const callListBlockfacesApi = async (): Promise<any> => {
    const maxResults = 500;
    const queryStr = `latitude=${latitude}&longitude=${longitude}&maxResults=${maxResults}`;

    console.log(`Calling ListBlockfaces API for ${queryStr}...`);
    const res = await fetch(`${SAMSPA_API_ROOT}/blockfaces?${queryStr}`);
    if (!res.ok) {
      throw new Error(res.statusText);
    }
    return await res.json();
  };

  const callGetBlockfaceOccupancyDataApi = async (
    blockfacesGeojsonInput: BlockfaceGeojson
  ): Promise<any> => {
    const weekday = getCurrentIsoWeekday();
    const time = getCurrentIsoTime();
    const weekdayTimeQueryStr = `weekday=${weekday}&time=${time}`;
    const blockfaceIdsQueryStr = blockfacesGeojsonInput.features
      .map(
        (x: GeoJSON.Feature<GeoJSON.Geometry, any>) =>
          `id=${x.properties.blockface_id}`
      )
      .join("&");

    console.log(
      `Calling GetBlockfaceOccupancyData API for ${weekdayTimeQueryStr}...`
    );
    const res = await fetch(
      `${SAMSPA_API_ROOT}/blockfaces/occupancy?${weekdayTimeQueryStr}&${blockfaceIdsQueryStr}`
    );
    if (!res.ok) {
      throw new Error(res.statusText);
    }
    return await res.json();
  };

  // TODO: Factor out api related code to a separate file
  const callGetDirectionsApi = async (
    origin: string,
    destination: string
  ): Promise<any> => {
    // TODO: Store API key in the AWS Secrets Manager
    const res = await fetch(
      `${OPEN_ROUTE_API_ROOT}/directions/driving-car?api_key=${OPEN_ROUTE_API_KEY}&start=${origin}&end=${destination}`
    );
    if (!res.ok) {
      throw new Error(res.statusText);
    }
    return await res.json();
  };

  const onEach = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any>,
    layer: Layer
  ) => {
    const popupHeader = buildPopupHeader(feature);
    const popupString = buildPopupString(feature);
    layer.bindPopup(popupHeader + popupString);

    layer.on("click", function (e) {
      var additionalDetailsStr = "";

      const expectedNumSpacesStr = getExpectedNumSpaces(feature).toFixed(1);
      additionalDetailsStr += `<br /> Estimated # of available spots: ${expectedNumSpacesStr}`;
      layer.bindPopup(popupHeader + additionalDetailsStr + popupString);

      // Append the ETA information if we successfully retrieve it through the API
      var eta = "";
      const origin = `${longitude},${latitude}`;
      const destination = `${feature.properties.longitude},${feature.properties.latitude}`;
      callGetDirectionsApi(origin, destination).then((result) => {
        eta = result.features[0].properties.summary.duration;
        const etaString = buildEtaString(eta);
        additionalDetailsStr += `<br /> ETA: ${etaString}`;
        layer.bindPopup(popupHeader + additionalDetailsStr + popupString);
      });
    });
  };

  // TODO: There are indeed overlapping portions here and the onClick event handler
  // for individual layers. Consider making them DRY
  const getBlockfaceColor = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any>
  ): string => {
    const expectedNumSpaces = feature.properties.expected_num_spaces;
    const maxExpectedNumSpaces = feature.properties.max_expected_num_spaces;

    if (maxExpectedNumSpaces !== Number.NEGATIVE_INFINITY) {
      const gradient = chroma.scale(["red", "yellow", "green"]);
      var scale = +expectedNumSpaces / maxExpectedNumSpaces;
      if (scale > 1) {
        scale = 1;
      }
      const blockfaceColor = gradient(scale).toString();
      return blockfaceColor;
    }

    return "";
  };

  const buildEtaString = (eta: string): string => {
    const etaInMinutes = Math.ceil(+eta / 60.0);
    if (etaInMinutes <= 1) {
      return etaInMinutes + " minute";
    }
    return etaInMinutes + " minutes";
  };

  const buildPopupHeader = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any>
  ): string => {
    return `<strong> ${feature.properties.unitdesc}, ${feature.properties.side} Side</strong>`;
  };

  const buildPopupString = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any>
  ): string => {
    const base = `<br /> Type: ${feature.properties.parking_category}
     <br /> # of Spots: ${feature.properties.parking_spaces}`;

    var categoryDetail = "";
    switch (feature.properties.parking_category) {
      case ParkingCategory.PaidParking:
      case ParkingCategory.RestrictedParkingZone:
        if (feature.properties.parking_time_limit != null) {
          var parkingTimeLimitString = "";
          if (feature.properties.parking_time_limit < 60) {
            parkingTimeLimitString = `${feature.properties.parking_time_limit} minutes`;
          } else {
            const parkingTimeLimitInHours =
              feature.properties.parking_time_limit / 60;
            parkingTimeLimitString = `${parkingTimeLimitInHours} hour`;
            if (parkingTimeLimitInHours > 1) {
              parkingTimeLimitString += "s";
            }
          }

          if (parkingTimeLimitString !== "") {
            categoryDetail += `<br /> Time Limit: ${parkingTimeLimitString}`;
          }
        }

        // Find day of the week
        const currentDate = new Date();
        // getDay() returns 0 to 6 for Sunday through Saturday
        var keyPrefix = "wkd";
        if (currentDate.getDay() === 0) {
          keyPrefix = "sun";
        } else if (currentDate.getDay() === 6) {
          keyPrefix = "sat";
        }
        if (
          feature.properties[`start_time_${keyPrefix}`] !== null &&
          feature.properties[`end_time_${keyPrefix}`] !== null
        ) {
          var startTimeString = feature.properties[`start_time_${keyPrefix}`];
          var endTimeString = feature.properties[`end_time_${keyPrefix}`];
          categoryDetail += buildPaidParkingTimeString(
            startTimeString,
            endTimeString
          );
        }

        categoryDetail += `<br /> Parking Rate: ${findCurrentRate(
          feature,
          currentDate,
          keyPrefix
        )}`;
        break;
      case ParkingCategory.TimeLimitedParking:
        categoryDetail += `<br /> Parking Time Limit: ${feature.properties.parking_time_limit} minutes`;
        break;
    }

    return base + categoryDetail;
  };

  const buildPaidParkingTimeString = (
    startTimeString: string,
    endTimeString: string
  ): string => {
    if (startTimeString.charAt(0) === "0") {
      startTimeString = startTimeString.slice(1);
    }
    if (endTimeString.charAt(0) === "0") {
      endTimeString = endTimeString.slice(1);
    }
    return `<br /> Paid Parking Time: ${startTimeString} - ${endTimeString}`;
  };

  const findCurrentRate = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any>,
    currentDate: Date,
    keyPrefix: string
  ): string => {
    const currentHour = currentDate.getHours();
    const currentMinute = currentDate.getMinutes();
    const currentTimeInMinutes = currentHour * 60 + currentMinute;

    const rate1Key = `${keyPrefix}_rate1`;
    const rate2Key = `${keyPrefix}_rate2`;
    const rate3Key = `${keyPrefix}_rate3`;

    if (feature.properties[rate1Key] != null) {
      const start1Key = `${keyPrefix}_start1`;
      const end1Key = `${keyPrefix}_end1`;
      if (feature.properties[start1Key] > currentTimeInMinutes) {
        return "Free";
      } else if (feature.properties[end1Key] > currentTimeInMinutes) {
        return rateString(feature.properties[rate1Key]);
      }
    }

    if (feature.properties[rate2Key] != null) {
      const start2Key = `${keyPrefix}_start2`;
      const end2Key = `${keyPrefix}_end2`;
      if (feature.properties[start2Key] > currentTimeInMinutes) {
        return "Free";
      } else if (feature.properties[end2Key] > currentTimeInMinutes) {
        return rateString(feature.properties[rate2Key]);
      }
    }

    if (feature.properties[rate3Key] != null) {
      const start3Key = `${keyPrefix}_start3`;
      const end3Key = `${keyPrefix}_end3`;
      if (feature.properties[start3Key] > currentTimeInMinutes) {
        return "Free";
      } else if (feature.properties[end3Key] > currentTimeInMinutes) {
        return rateString(feature.properties[rate3Key]);
      }
    }

    return "Free";
  };

  const rateString = (rateValue: string): string => {
    return `$${rateValue}/hour`;
  };

  useEffect(() => {
    callListBlockfacesApi().then((resp) => {
      console.log(`Got nearest ${resp.blockfaces.length} blockfaces`);
      let features = resp.blockfaces.map(
        (blockfaceSummary: BlockfaceSummary) => ({
          type: "Feature",
          properties: blockfaceSummary,
          geometry: JSON.parse(blockfaceSummary.geometry),
        })
      );
      let blockfaceGeojson = {
        type: "FeatureCollection",
        name: `${latitude}#${longitude}`,
        features: features,
      };

      callGetBlockfaceOccupancyDataApi(
        blockfaceGeojson as BlockfaceGeojson
      ).then((resp) => {
        console.log(
          `Got block occupancy data for ${resp.blockface_occupancy_data_results.length} blockfaces`
        );

        var featureMap: {
          [key: string]: GeoJSON.Feature<GeoJSON.Geometry, any>;
        } = {};
        blockfaceGeojson.features.forEach(
          (x: GeoJSON.Feature<GeoJSON.Geometry, any>) =>
            (featureMap[x.properties.blockface_id] = x)
        );

        var maxExpectedNumSpaces = Number.NEGATIVE_INFINITY;
        resp.blockface_occupancy_data_results.forEach(
          (result: BlockfaceOccupancyDataResult) => {
            if (result.blockface_id in featureMap) {
              featureMap[result.blockface_id].properties.times = result.times;
              featureMap[result.blockface_id].properties.historical_averages =
                result.historical_averages;
              const expectedNumSpaces = getExpectedNumSpaces(
                featureMap[result.blockface_id]
              );
              featureMap[result.blockface_id].properties.expected_num_spaces =
                expectedNumSpaces;
              maxExpectedNumSpaces = Math.max(
                maxExpectedNumSpaces,
                expectedNumSpaces
              );
            }
          }
        );

        resp.blockface_occupancy_data_results.forEach(
          (result: BlockfaceOccupancyDataResult) => {
            if (result.blockface_id in featureMap) {
              featureMap[
                result.blockface_id
              ].properties.max_expected_num_spaces = maxExpectedNumSpaces;
            }
          }
        );

        setBlockfaceGeojson(blockfaceGeojson);
      });
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [latitude, longitude]);

  // Style function for individual features. This basically takes care of addiing correct
  // style to individual layers (features).
  const styleFeature = (
    feature: GeoJSON.Feature<GeoJSON.Geometry, any> | undefined
  ): PathOptions => {
    if (feature) {
      return {
        weight: blockfaceWeight,
        color: getBlockfaceColor(feature),
      };
    }

    return {
      weight: 1,
    };
  };

  useEffect(() => {
    const layer = geoJsonRef.current;
    if (layer) {
      layer.clearLayers().addData(blockfaceGeojson as GeoJsonObject);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [blockfaceGeojson]);

  const setMarkerToCurrentLocation = () => {
    if ("geolocation" in navigator) {
      console.log("Geolocation is available");
      navigator.geolocation.getCurrentPosition((position) => {
        setLatitude(position.coords.latitude);
        setLongitude(position.coords.longitude);
        setShowSearchResult(false);
      });
    } else {
      console.log("Geolocation is NOT available");
    }
  };

  useEffect(() => {
    setMarkerToCurrentLocation();
  }, []);

  return (
    <MapContainer
      center={[latitude, longitude]}
      zoom={17}
      scrollWheelZoom={true}
      zoomControl={false}
    >
      <TileLayer
        attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
      <GeoJSON
        ref={geoJsonRef}
        // only re-renders if key changes
        data={blockfaceGeojson as GeoJsonObject}
        onEachFeature={onEach}
        style={styleFeature}
      />
      <SetViewOnClick coords={[latitude, longitude]} />
      <Marker position={[latitude, longitude]} />
      <DragOrZoomEvent />
      <SearchComponent
        noResult={noResult}
        showSearchResult={showSearchResult}
        setNoResult={setNoResult}
        setShowSearchResult={setShowSearchResult}
        setLatitude={setLatitude}
        setLongitude={setLongitude}
        setMarkerToCurrentLocation={setMarkerToCurrentLocation}
      />
    </MapContainer>
  );
}

export default MapComponent;
