import { OpenStreetMapProvider } from "leaflet-geosearch";
import { useState, KeyboardEvent } from "react";
import {
  Box,
  createTheme,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  TextField,
  ThemeProvider,
} from "@mui/material";
import { NearMe } from "@mui/icons-material";

interface Location {
  address: string;
  latitude: number;
  longitude: number;
}

type SearchComponentProps = {
  noResult: boolean;
  showSearchResult: boolean;
  setNoResult: React.Dispatch<React.SetStateAction<boolean>>;
  setShowSearchResult: React.Dispatch<React.SetStateAction<boolean>>;
  setLatitude: React.Dispatch<React.SetStateAction<number>>;
  setLongitude: React.Dispatch<React.SetStateAction<number>>;
  setMarkerToCurrentLocation: () => void;
};

function SearchComponent({
  noResult,
  showSearchResult,
  setLatitude,
  setLongitude,
  setShowSearchResult,
  setNoResult,
  setMarkerToCurrentLocation,
}: SearchComponentProps) {
  const [searchResult, setSearchResult] = useState(new Set<Location>());
  const provider = new OpenStreetMapProvider({
    params: {
      "accept-language": "us",
      countrycodes: "us",
    },
  });

  const theme = createTheme({
    components: {
      // Name of the component
      MuiTextField: {
        styleOverrides: {
          // Name of the slot
          root: {
            // Some CSS
            backgroundColor: "white",
            borderRadius: "30px",
            width: "80%",
            position: "absolute",
            left: "10%",
          },
        },
      },
      MuiInputLabel: {
        styleOverrides: {
          root: {
            marginLeft: "5%",
          },
        },
      },
      MuiInput: {
        styleOverrides: {
          root: {
            "&&&:before": {
              borderBottom: "none",
            },
            "&&:after": {
              borderBottom: "none",
            },
            marginLeft: "5%",
          },
        },
      },
      MuiList: {
        styleOverrides: {
          root: {
            width: "72%",
            position: "absolute",
            left: "14%",
            marginTop: "60px",
            backgroundColor: "white",
          },
        },
      },
    },
  });

  const onSearch = (e: KeyboardEvent<HTMLInputElement>) => {
    // Only when enter key is pressed
    if (e.code === "Enter") {
      const fetchData = async () => {
        const results = await provider.search({
          query: (e.target as HTMLInputElement).value,
        });

        if (results.length === 0) {
          setNoResult(true);
        } else {
          var addresses: Location[] = [];
          results.forEach((element) => {
            addresses.push({
              address: element.label,
              latitude: element.y,
              longitude: element.x,
            });
          });

          setNoResult(false);
          setSearchResult(new Set(addresses));
        }
      };

      // call the function
      fetchData()
        // make sure to catch any error
        .catch(console.error);
    } else {
      // TODO: In the future, we might want to auto-complete the search result by
      // performing the search every time there is a change in the text field
      // But not sure if it would be possible with the OSM's free tier limit
      console.log("Key other than enter pressed");
      setNoResult(false);
    }
  };

  const setLocation = (location: Location) => {
    setLatitude(location.latitude);
    setLongitude(location.longitude);
    setShowSearchResult(false);
  };

  const onSearchBarClick = () => {
    setShowSearchResult(true);
  };

  return (
    <div>
      <Box sx={{ width: "100%", bgcolor: "background.paper" }}>
        <ThemeProvider theme={theme}>
          <TextField
            variant="standard"
            label="Search"
            margin="normal"
            onKeyDown={onSearch}
            onFocus={onSearchBarClick}
          />
          {showSearchResult && (
            <List>
              <ListItem
                disablePadding
                onClick={() => setMarkerToCurrentLocation()}
              >
                <ListItemButton>
                  <ListItemIcon>
                    <NearMe />
                  </ListItemIcon>
                  <ListItemText primary={"Your location"} />
                </ListItemButton>
              </ListItem>
              {noResult && (
                <ListItem disablePadding>
                  <ListItemText
                    primary={"No result :( Try other keywords for search"}
                  />
                </ListItem>
              )}
              {!noResult &&
                Array.from(searchResult).map((element) => {
                  return (
                    <ListItem
                      disablePadding
                      key={element.address}
                      onClick={() => {
                        setLocation(element);
                      }}
                    >
                      <ListItemButton>
                        <ListItemText primary={element.address} />
                      </ListItemButton>
                    </ListItem>
                  );
                })}
            </List>
          )}
        </ThemeProvider>
      </Box>
    </div>
  );
}

export default SearchComponent;
